'use strict';

/**
* Sort a strided array.
*
* @module @stdlib/blas-ext-base-gsort
*
* @example
* import gsort from '@stdlib/blas-ext-base-gsort';
*
* var x = [ 1.0, -2.0, 3.0, -4.0 ];
*
* gsort( x.length, 1.0, x, 1 );
* // x => [ -4.0, -2.0, 1.0, 3.0 ]
*
* @example
* import gsort from '@stdlib/blas-ext-base-gsort';
*
* var x = [ 1.0, -2.0, 3.0, -4.0 ];
*
* gsort.ndarray( x.length, 1.0, x, 1, 0 );
* // x => [ -4.0, -2.0, 1.0, 3.0 ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;
